
import { defineComponent, ref } from "vue";
import { config } from "../ts/Fetcher";

export default defineComponent({
  setup(props) {
    let flipped = ref(false);
    let bgImg = ref("scooter.png");

    function flip() {
      flipped.value = !flipped.value;
    }

    function buy(prodIndex: string) {
      fetch("https://gm_truckrental/buy", { method: "post", body: prodIndex });
    }

    return {
      config,
      props,
      flipped,
      flip,
      bgImg,
      buy,
    };
  },
  props: {
    index: {
      default: "0",
      type: String,
    },
    name: String,
    price: String,
    image: String,
    additional: Array,
  },
});
