<div class="product-card-container" :class="{ moreinfo: flipped }"><div class="product-card"><div class="front"><div class="img-container"><div class="img" :style="{ backgroundImage: `url('./${props.image}')` }"></div></div><div class="info"><h1 class="productname">{{ props.name }}</h1><div class="pricetag">{{ props.price }}</div></div><div class="btns-container"><div class="btns"><div class="btn-container" @click="flip()"><div class="btn moreinfo">{{ config.langMoreInfo }}</div></div><div class="btn-container" @click="buy(props.index)"><div class="btn buybtn">{{ config.langBuy }}</div></div></div></div></div><div class="back"><div class="textcontainer"> <div class="textcontent" v-for="paragraph in props.additional">{{ paragraph }}</div></div><div class="back-btn-container-outer"><div class="back-btn-container-inner"><div class="null"></div><div class="btn-container"><div class="bbtn" @click="flip()">{{ config.langBack }}</div></div><div class="null"></div></div></div></div></div></div>