
import { defineComponent, ref, onMounted } from "vue";
import makeDraggable from "../ts/ui/DragFunctions";
import { config, products } from "../ts/Fetcher";
import ProductCard from "./ProductCard.vue";

export default defineComponent({
  setup(props) {
    const draggable = ref(props.isDraggable);
    const root = ref<HTMLDivElement | null>(null);
    let windowClosed = ref(true);

    function closeWindow(): void {
      windowClosed.value = true;
      fetch("https://gm_truckrental/close", { method: "post" });
    }
    function openWindow(): void {
      windowClosed.value = false;
    }

    onMounted(() => {
      if (root.value) {
        makeDraggable(root.value);
      }
      window.addEventListener("message", (e: MessageEvent) => {
        let dat = e.data;

        switch (dat.type) {
          case "gm_window_open":
            openWindow();
            break;
          case "gm_window_close":
            closeWindow();
            break;
        }
      });
      window.addEventListener("keydown", (e) => {
        if (e.keyCode === 27) {
          closeWindow();
        }
      });
    });

    return {
      draggable,
      root,
      config,
      products,
      windowClosed,
      closeWindow,
    };
  },
  components: {
    ProductCard,
  },
  props: {
    isDraggable: Boolean,
  },
});
