<div id="storewindow" ref="root" :style="{ '--scale': config.scale, '--bg-primary': config.bgPrimary, '--bg-secondary': config.bgSecondary, '--trafficlight-active': config.tlActive, '--trafficlight-inactive': config.tlInactive }" :class="{ closed: windowClosed }"><div class="gm-frame">{{ config.langShopname }}<div class="close-btn" @click="closeWindow"></div></div><div class="card-container"><ProductCard v-for="p in products" :key="p.id" :index="p.id" :name="p.name" :price="p.price" :image="p.image" :additional="p.additionalInfo"></ProductCard></div></div>